import React from "react";
import Typography from "../../../../components/Typography";
import Button from "@material-ui/core/Button";
import styles from "./styles.module.scss";
import stepStyles from "../styles.module.scss";

const Prompt = ({ index, goTo, next, skip, stepDifference }: SurveyStepProps) => {
    const className = stepDifference < 0
        ? stepStyles.previous
        : (
            stepDifference > 0
                ? stepStyles.upcoming
                : stepStyles.current
        );

    const buttonAttributes = stepDifference !== 0
        ? { onClick: () => goTo(index) }
        : {};

    return (
        <div className={ className } { ...buttonAttributes }>
            <Typography variant={ stepDifference === 0 ? "h3" : "h6" }>1. Take Survey?</Typography>
            { stepDifference === 0 && (
                <div className={ stepStyles.card }>
                    <Typography variant="body1">Fill out a quick survey to get your budgeting goals started?</Typography>
                    <div className={ styles.buttons }>
                        <Button variant="outlined" color="primary" onClick={ next } className={ stepStyles.topMargin }>Continue</Button>
                        <Button variant="outlined" color="primary" onClick={ skip } className={ stepStyles.topMargin }>No Thanks</Button>
                    </div>
                </div>
            ) }
        </div>
    );
};

export default Prompt;
